}

.link {
  font-family: "Press Start 2P", cursive;
  text-decoration: none;
  color: white;
  font-weight: bold;
}

a {
  color: inherit;
  text-decoration: none;
  letter-spacing: 0.15em;
  display: inline-block;
  padding: 15px 20px;
  position: relative;
}

a.link:after {
  background: none repeat scroll 0 0 transparent;
  bottom: 0;
  content: "";
  display: block;
  height: 2px;
  left: 50%;
  position: absolute;
  background: white;

  transition: width 0.3s ease 0s, left 0.3s ease 0s;
  width: 0;
  text-decoration: none;
  color: inherit;
}

a.link:hover:after {
  width: 100%;
  left: 0;
  text-decoration: none;
  color: white;
}

