/* ANIMATED SKILL SET */

@keyframes load {
  from {
    width: 0%;
  }
}
@-webkit-keyframes load {
  from {
    width: 0%;
  }
}

.bar {
  background-color: #eee;
  padding: 2px;
  border-radius: 15px;
  margin-bottom: 5px;
  font-size: 14px;
  color: #fff;
  font-weight: bold;
  text-shadow: 1px 1px 1px rgba(0, 0, 0, 0.5);
}
.bar::before {
  content: attr(data-skill);
  background-color: rgb(176, 0, 181);
  display: inline-block;
  padding: 5px 0 5px 10px;
  border-radius: inherit;
  animation: load 2s 0s;
  -webkit-animation: load 2s 0s;
  -moz-animation: load 2s 0s;
  -o-animation: load 2s 0s;
}

.bar.front::before {
  background-color: #febaa4;
}
.bar.back::before {
  background-color: #674763;
}

.bar.yes::before {
  background-color: #ac92c9;
}

.bar.ok::before {
  background-color: #0a2830;
}

.bar.no::before {
  background-color: #825bae;
}

.bar.basic::before {
  width: calc(40% - 10px);
}
.bar.intermediate::before {
  width: calc(50% - 10px);
}
.bar.advanced::before {
  width: calc(70% - 10px);
}
.bar.expert::before {
  width: calc(80% - 10px);
}

input {
